import React from 'react'
import useStore from '../hooks/useStore'
import OrganizationFormDrawer from '../components/OrganizationFormDrawer'
import {Avatar, Heading, HStack} from '@chakra-ui/react'
import {observer} from 'mobx-react'
import Card from '../ui/Card'
import EditButton from '../components/EditButton'
import DisplayForRole, {EDIT_ROLES} from '../components/DisplayForRole'

const OrganizationProfile: React.FC = () => {
  const {organizationStore} = useStore()
  const {currentOrganization} = organizationStore

  if (!currentOrganization) {
    return null
  }

  const onSubmit = async (organization: any) => {
    await organizationStore.update({
      ...currentOrganization,
      ...organization,
    })
  }

  return (
      <Card>
        <DisplayForRole roles={EDIT_ROLES}>
          <OrganizationFormDrawer onSubmit={onSubmit} organization={currentOrganization}/>
        </DisplayForRole>
        <HStack spacing={4}>
          <Avatar src={currentOrganization.logo} name={currentOrganization.name}/>
          <Heading size="md">{currentOrganization.name}</Heading>
          <EditButton to="?organization"/>
        </HStack>
      </Card>
  )
}

export default observer(OrganizationProfile)