import RootService from '../services'
import RootStore from './index'
import AbstractResourceStore from './AbstractResourceStore'
import ProjectTag from '../domain/ProjectTag'
import ProjectTagService from '../services/ProjectTagService'
import {computed} from 'mobx'
import Project from '../domain/Project'
import Tag from '../domain/Tag'

export default class ProjectTagStore extends AbstractResourceStore<ProjectTag> {
  private readonly projectTagService: ProjectTagService
  private readonly rootStore: RootStore

  public constructor(rootService: RootService, rootStore: RootStore) {
    super(rootService.projectTagService, rootStore.accountStore)
    this.projectTagService = rootService.projectTagService
    this.rootStore = rootStore
  }

  public async loadByProject(projectId: string) {
    await this.setManyEventually(this.projectTagService.getAllByProject(projectId))
  }

  protected prepareForStoring(item: ProjectTag): ProjectTag {
    return super.prepareForStoring(new StoredProjectTag(this.rootStore).fromObject(item))
  }

  public getItemsByProject(projectId: string): ProjectTag[] {
    return this.items.filter(i => i.projectId === projectId)
  }
}

class StoredProjectTag extends ProjectTag {
  private readonly rootStore: RootStore

  constructor(rootStore: RootStore) {
    super()
    this.rootStore = rootStore
  }

  @computed
  get project(): Project {
    if (this.projectId && !this._project) {
      return this.rootStore.projectStore.getByIdOrLoad(this.projectId)
    }

    return this._project
  }


  @computed
  get tag(): Tag {
    if (this.tagId && !this._tag) {
      return this.rootStore.tagStore.getByIdOrLoad(this.tagId)
    }

    return this._tag
  }
}