import React from 'react'
import Form, {FormProps} from './Form'
import {DrawerBody, DrawerFooter, DrawerHeader} from '../ui/Drawer'
import FormSubmitButton, {FormSubmitButtonProps} from './FormSubmitButton'
import RouterDrawer from './RouterDrawer'
import {UseFormMethods} from 'react-hook-form'

interface DrawerFormProps extends Omit<FormProps, 'children' | 'onSubmit'> {
  header?: React.ReactNode
  submitButton?: FormSubmitButtonProps & { text: string }
  name: string
  children: React.ReactNode | ((context: { formContext: UseFormMethods, drawerContext: string }) => any)
  onSubmit: (values: object, drawerValue: string) => void | Promise<void>
  autoClose?: boolean
}

const DrawerForm: React.FC<DrawerFormProps> = ({submitButton, name, header, children, onSubmit, autoClose, ...formProps}) => {
  return (
      <RouterDrawer name={name}>
        {((drawerContext) => {
          const handleSubmit = async (values: any) => {
            const result = await onSubmit(values, drawerContext.value)

            if (autoClose) {
              drawerContext.handleClose()
            }

            return result
          }

          return (
              <Form onSubmit={handleSubmit} {...formProps}>
                {(formContext) => (
                    <>
                      {header && <DrawerHeader>{header}</DrawerHeader>}

                      <DrawerBody>
                        {typeof children === 'function' ? (children as any)({ formContext, drawerContext }) : children}
                      </DrawerBody>

                      {submitButton && (
                          <DrawerFooter>
                            <FormSubmitButton {...submitButton}>{submitButton.text}</FormSubmitButton>
                          </DrawerFooter>
                      )}
                    </>
                )}
              </Form>
          )
        })}
      </RouterDrawer>
  )
}

DrawerForm.defaultProps = {
  submitButton: {
    text: 'Save'
  },
  autoClose: true
}

export default DrawerForm